.QuizContainer {
  min-width: 15rem;
  width: 100%;
  background-color: #fff;
}

.QuizBodyContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.QuizHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  background-color: #20221D;
  height: 3.75rem;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: #fff;
}

.QuestionNumber {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  //border-right: 3px solid #174665;
  background-color: #339DE5;
  width: 3.75rem;
}

.QuestionCategory {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  text-align: left;
  padding: 0 1rem;
}

.QuestionImage {
  cursor: pointer;
  color: #555;
}

.QuestionImage:hover {
  color: #222;
}

.QuizBody {
  padding: 1rem;
  width: 100%;
}

.QuizCheckbox {
  border-radius: 50%;
}

.QuestionText {
  padding: 1rem 0;
}

.QuizBody ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.QuizBody li {
  background-color: #fff;
}

.QuizBody li:hover {
  background-color: #339DE5;
  color: #fff;
}

.AnswerOption {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.85rem 0;
  cursor: pointer;
  margin-bottom: 0.5rem;
  color: #555;
  line-height: 1rem;
}

.AnswerOption:hover {
  color: #333;
}

.AnswerInput {
  margin: 0 0.75rem 0 0;
  cursor: pointer;
}

.disabled .AnswerInput {
  cursor: auto;
}

.disabled {
  cursor: auto;
}

.disabled:hover {
  color: #555;
}

.incorrectAnswer {
  //background-color: #EF4836;
  background-color: #FF7256;
  color: #fff;
}

.incorrectAnswer:hover, .correctAnswer:hover {
  color: #fff;
}

.correctAnswer {
  background-color: #2ECC71;
  color: #fff;
}

.NextQuestion {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 3.75rem;
  background-color: #333;
}

.UserScore {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-color: #555;
  color: #fff;
  -ms-flex-preferred-size: 50%;
      flex-basis: 50%;
  -ms-flex-negative: 0;
      flex-shrink: 0;
}

.UserProgress {
  position: absolute;
  top:0;
  bottom: 0;
  left:0;
  background-color: #2ECC71;
  height: 100%;
}

.Score {
  z-index: 1;
}

.NextQuestionButton {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 100%;
  background-color: #333;
  cursor: pointer;
  border: none;
  padding: 0px;
  -webkit-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  transition: background-color 0.3s;
}

.NextQuestionButton:hover {
  background-color: #222;
}

@media screen and (max-width: 48.75rem) {
  .QuizContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    height: 100%;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
}

.Hidden {
  display: none;
}

.visible {
  cursor: pointer;
}

.UserProgressBarContainer {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  //padding: 1rem;
}

.UserProgressBar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 3rem;
}

.UserProgressBarIncomplete {
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 100%;
  background-color: #222;
}

.UserProgressBarCorrect {
  height: 100%;
  background-color: #2ECC71;
}

.UserProgressLabelContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  width: 2rem;
  //justify-content: flex-start;
}

.UserProgressArrow {
  width: 0;
  height: 0;
  border-left: 0.5rem solid transparent;
  border-right: 0.5rem solid transparent;
  border-bottom: 1rem solid #222;
  margin-left: -0.5rem;
}

.UserProgressLabel {
  width: 100%;
  text-align: center;
  margin-left: -1rem;
}



